<template lang="pug">
    .editable(contentEditable='true' @input="update", @keypress='limit($event)')
</template>

<script>
    import anchorme from "anchorme";

    export default {
        name: 'w-editable',
        props:{
            content: '',
            maxLenght: {
                type: Number,
                default: 9999
            }
        },
        data () {
            return {
                msg: 'Welcome to Your Vue.js App'
            }
        },
        mounted:function(){
            this.$el.innerText = this.content;
        },
        methods:{
            urlify(text) {
                var urlRegex = /(https?:\/\/[^\s]+)/g;
                return text.replace(urlRegex, function(url) {
                    return '<a href="' + url + '">' + url + '</a>';
                })
            },
            update:function(event){
                this.$emit('update', this.urlify(event.target.innerText));
            },
            limit(ev){
                if (ev.target.innerText.length >= this.maxLenght) {
                    ev.preventDefault();
                }
            }
        }
    }
</script>
<style lang="stylus" scope src="@/assets/styles/common/Editable.styl"></style>


